      </div>
      <div class="cont_title2">
        为确保你的投保可回溯追踪，你的投保行为和信息将会被记录。
      </div>
    </div>

    <div class="content2">
      <div class="view_details">
        <div class="view_details_title">
          建信人寿旅行平安保险产品计划（1天）
        </div>
        <div class="view_details_but">查看详情</div>
      </div>
    </div>
    <div class="ticket_buyer">
      <div class="ticket_buyer_cont">
        <div class="ticket_buyer_title">购票人信息</div>
        <ticketBuyerInfo
          v-for="(item, index) in childrenData"
          :key="index"
          :infoData="item"
          :data-index="index"
          :ref="`child${index}`"
          @form-submit="handleFormSubmit"
          @form-validation-failed="handleValidationFailed"
          @infoSelectedPerson="infoSelectedPerson"
        ></ticketBuyerInfo>

        <div class="material">
          <van-checkbox :value="hasAgree" icon-size="4vw">
            我已阅读并同意
            <span
              @click.stop="pdfPreview(item, index)"
              :key="item.materialType"
              v-for="(item, index) in 1"
              >《保险条款》《投保须知》《投保声明》《告知事项》</span
            >
          </van-checkbox>
        </div>
      </div>
    </div>
    <div class="approval_bot">
      <div class="approval_bot_text">
        建信人寿将在被保险人首次进入滑雪场时审核投保信悬，保险责任于下个整点时间生效，具体保险期间和保险责任以建信人寿签发的保险合同为准。你可以登录建信人寿公众号查询下载电子保险合同。若审核未通过，建信人寿将取消本次投保，不收取保费，不承担保险责任。
      </div>

      <div class="material">
        <van-checkbox :value="hasAgree" icon-size="4vw">
          <span style="color: red; font-size: 3.8vw"
            >我已知晓上述内容，同意建信人寿申核投保信息。</span
          >
        </van-checkbox>
      </div>
      <div style="margin: 16px">
        <van-button round block type="info" @click="handleConfirm"
          >立即预约</van-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import ticketBuyerInfo from "./components/ticketBuyerInfo/index.vue";

export default {
  components: { ticketBuyerInfo },
  data() {
    return {
      hasAgree: false,
      selectPolicyIndex: "",
      childrenData: [
        {
          form: {
            name: "张*是1",
            idCard: "3429************5451",
            phone: "15123456789",
            idType: "1010",
            ChoosePlan: "ss",
          },
          data: {
            id: "1",
            price: "65",
            name: "张*是  ",
            idCard: "3429************5456",
            phone: "15123456781",
            checked: true,
          },
        },
        {
          form: {
            name: "张*是1",
            idCard: "3429************5451",
            phone: "15123456789",
            idType: "1010",
            ChoosePlan: "ss",
          },
          data: {
            id: "1",
            price: "65",
            name: "张*是  ",
            idCard: "3429************5456",
            phone: "15123456781",
            checked: true,
          },
        },
      ],
    };
  },

  methods: {
    // 点击选择投保人传值
    infoSelectedPerson(e) {
      this.childrenData[e.selectPolicyIndex].form = e.selectedPerson;
      this.$set(this.childrenData[e.selectPolicyIndex].form, "disabled", true);
      console.log(e, this.childrenData);
    },
    async handleConfirm() {
      const submitPromises = this.childrenData.map((_, index) => {
        return new Promise((resolve) => {
          this.$refs[`child${index}`][0].$refs.form.submit(); // 提交表单
          const isonSubmitCheck = setInterval(() => {
            if (this.$refs[`child${index}`][0].isonSubmit !== undefined) {
              clearInterval(isonSubmitCheck); // 清除定时器
              resolve(this.$refs[`child${index}`][0].isonSubmit); // 返回 isonSubmit 的值
            }
          }, 100); // 每100ms检查一次
        });
      });

      const results = await Promise.all(submitPromises); // 等待所有提交完成

      const allValid = results.every((result) => result === true); // 检查是否全部为 true
      if (allValid) {
        console.log("所有表单验证通过，进行后续操作");
        // 执行后续逻辑
      } else {
        console.log("某些表单验证失败", results);
        // 处理验证失败的情况
      }
    },

    handleFormSubmit(formData) {
      // 处理表单提交逻辑
      console.log("表单数据：", formData);
    },
    handleValidationFailed() {
      // 处理表单验证失败的逻辑
      this.$toast("表单验证失败，请检查输入");
    },

    pdfPreview() {},
  },
};
</script>

