<template>
  <div class="view-info">
    <!-- 顶部图片 -->
    <van-image :src="batchSrc" width="100%" radius="10px" class="image">
      <van-loading
        slot="loading"
        type="spinner"
        size="40"
        vertical
      ></van-loading>
      <template v-slot:error>
        <van-image :src="src" width="100%" radius="10px" class="image">
          <van-loading
            slot="loading"
            type="spinner"
            size="40"
            vertical
          ></van-loading>
        </van-image>
      </template>
    </van-image>
    <!-- 保障详情 -->
    <div class="detail_box">
      <div class="title">保障详情
         <span class="view_details" @click="todetail" v-if="insuranceDetail">查看详情</span>
      </div>
      <div class="detail" @click="showAll = !showAll">
         <div
          class="detail_insurance"
          :data-detail="'投保年龄'"
          :data-money="agePeriod"
        ></div>
        <div
          class="detail_insurance"
          :data-detail="'保障期间'"
          :data-money="insurePeriod"
        ></div>
        <div>
          <div
            class="detail_insurance"
            :data-detail="'职业等级'"
            :data-money="occupationalGrade"
              v-show="
              productData.occupationalGrade &&
              productData.occupationalGrade != ''
            "    
          >     
            <!-- <span
              class="grade"
               v-show="
                productData.materialPath &&
                productData.materialPath != ''
              "
              @click="viewDetail"
              >《职业等级》</span
            > -->
          </div>
          <!-- <van-popup
            v-model="showGradePdf"
            style="height: 100%; width: 100%"
            position="right"
          >
            <div class="pdf">
              <img 
            src=@/assets/svg/crossSign.svg @click="showGradePdf = false" class="close_gradepdf">
              <pdf
                :src="gradePdfsrc"
                :page="currentPage"
                @num-pages="pageCount = $event"
                @loaded="currentPage = 1"
              ></pdf>
              <div class="gradePdfsrc">
                <span @click="currentPage > 1 && currentPage--">上一页</span>
                <span
                  >当前第{{ currentPage || 0 }}页/共{{
                    pageCount || "∞"
                  }}页</span
                >
                <span @click="currentPage < pageCount && currentPage++"
                  >下一页</span
                >
              </div>
            </div>
          </van-popup> -->
        </div>
        <div
          v-for="(item, i) in liability"
          :key="i"
          class="detail_insurance"
          :data-detail="item.ilName"
          :data-money="item.description"
        ></div>

        <!--对职业等级的处理，如果没有职业等级那么职业等级字段和文字都不显示，如果有职业等级字段在判断是否有该文件
        否则不显示该文件-->

        <!--点击收起和查看更多-->
        <span
          v-if="productData.prdInsuranceDutyList.length > 2"
          class="more"
          @click.stop="showAll = !showAll"
          >{{ showAll ? "点击收起" : "查看更多" }}</span
        >
      </div>
    </div>
    <!-- 生效起期 -->
    <div class="final-time">
      <div v-if="productData.takeEffectFlag == 1">
        <div class="bubble-tip" v-if="!info.date">请在这里选择保险生效日期</div>
        <!-- :disabled="privates.date.disabled" -->
        <time-picker
          v-model="info.date"
          class="nopadd"
          type="date"
          padding="0"
          :disabled="privates.date.disabled"
          text-align="center"
          :min-date="minDate"
          :max-date="maxDate"
          placeholder="投保后2小时生效"
        >
          <div class="form_title">保单生效日</div>
        </time-picker>
      </div>
      <div v-else>
        <div class="bubble-tip" v-if="!info.date">请在这里选择保险生效日期</div>
        <!-- :disabled="privates.date.disabled" -->
        <date-picker
          v-model="info.date"
          class="nopadd"
          type="date"
          padding="0"
          :disabled="privates.date.disabled"
          text-align="center"
          :min-date="minDate"
          :max-date="maxDate"
          placeholder="生效日的0时生效"
        >
          <div class="form_title">保单生效日</div>
        </date-picker>
      </div>
    </div>
    <!-- 投保信息 -->
    <div class="policy-info">
      <div class="policy-title">{{ forWho }}</div>
      <div class="policy-rela" v-if="rela.length > 0">
        <div
          :class="[{ active: item.active }, 'rela_info', item.className]"
          v-for="item in rela"
          :key="item.code"
          :data-name="item.name"
          @click.stop="chooseRela(item, privates.choosedRela.disabled)"
        >
          <img :src="item.src" alt />
          <svg width="56" height="56" class="svg">
            <circle
              class="circle"
              cx="27"
              cy="27"
              r="26"
              stroke-width="2"
              stroke="#4ca2fe"
              fill="none"
              :stroke-dasharray="item.active ? '157 0' : '0 157'"
            ></circle>
          </svg>
        </div>
      </div>
      <div class="form">
        <transition name="van-slide-left">
          <div v-show="info.choosedRela !== '01'">
            <e-input
              v-model="info.insureName"
              :disabled="privates.insureName.disabled"
              :readonly="readonly_iNa"
              textAlign="left"
              placeholder="被保人姓名"
              placeholderColor="#bec1c8"
              ><img class="input_icon icon_name" style="vertical-align: middle;"
              src=@/assets/svg/name.svg></e-input
            >
            <e-input
              v-model="info.insureIdCard"
              textAlign="left"
              :disabled="privates.insureIdCard.disabled"
              placeholder="身份证号码"
              type="idCard"
              placeholderColor="#bec1c8"
              mask="999999 99999999 999S"
              :maxlength="20"
              ><img class="input_icon icon_name" style="vertical-align: middle;"
              src=@/assets/svg/idCard.svg></e-input
            >
            <div class="date-picker-idcard">
              <date-picker
                textAlign="left"
                placeholder="身份证有效期"
                placeholderColor="#bec1c8"
                type="date"
                :min-date="cardDate"
                :max-date="new Date('2050/12/31')"
                v-model="info.insureIdDate"
                ><img class="input_icon icon_name" style="vertical-align:
                middle;" src=@/assets/svg/cardDate.svg></date-picker
              >
              <div
                :class="['forever-button', { active: info.insureForever }]"
                @click="foreverHandle('insure')"
              >
                长期
              </div>
            </div>
            <div class="policy-title before-line">{{ "投保人信息" }}</div>
          </div>
        </transition>
        <e-input
          v-model="info.policyName"
          :disabled="privates.policyName.disabled"
          :readonly="readonly_pNa"
          :filterType="privates.policyName.filterType"
          textAlign="left"
          :placeholder="info.choosedRela !== '01' ? '投保人姓名' : '被保人姓名'"
          placeholderColor="#bec1c8"
          ><img class="input_icon icon_name" style="vertical-align: middle;"
          src=@/assets/svg/name.svg></e-input
        >
        <e-input
          :disabled="privates.policyIdCard.disabled"
          :readonly="readonly_pID"
          :filterType="privates.policyIdCard.filterType"
          v-model="info.policyIdCard"
          textAlign="left"
          type="idCard"
          placeholder="身份证号码"
          placeholderColor="#bec1c8"
          mask="999999 99999999 999S"
          :maxlength="20"
          ><img class="input_icon icon_name" style="vertical-align: middle;"
          src=@/assets/svg/idCard.svg></e-input
        >
        <div class="date-picker-idcard date-picker-line">
          <date-picker
            textAlign="left"
            placeholder="身份证有效期"
            placeholderColor="#bec1c8"
            type="date"
            :min-date="cardDate"
            :max-date="new Date('2050/12/31')"
            v-model="info.policyIdDate"
            ><img class="input_icon icon_name" style="vertical-align: middle;"
            src=@/assets/svg/cardDate.svg></date-picker
          >
          <div
            :class="['forever-button', { active: info.policyForever }]"
            @click="foreverHandle('policy')"
          >
            长期
          </div>
        </div>
        <e-input
          email
          textAlign="left"
          v-model="info.email"
          :maxlength="40"
          placeholder="请输入常用邮箱"
          placeholderColor="#bec1c8"
          ><img class="input_icon icon_name" style="vertical-align: middle;"
          src=@/assets/svg/email.svg></e-input
        >
        <e-input
          v-model="info.policyPhone"
          :disabled="privates.policyPhone.disabled"
          :readonly="readonly_pPh"
          :filterType="privates.policyPhone.filterType"
          textAlign="left"
          placeholder="手机号码"
          mask="999 9999 9999"
          placeholderColor="#bec1c8"
          @blur="phoneBlur(dataInfo)"
          ><img class="input_icon icon_name" style="vertical-align: middle;"
          src=@/assets/svg/phone.svg></e-input
        >
        <eArea
          :value="productData.plchdDistrictCode"
          :cityCode="productData.plchdCityCode"
          :readonly="true"
        ></eArea>
      </div>
      <div class="material" @click="readPdf">
        <van-checkbox :value="hasAgree" icon-size="4vw">
          我已阅读并同意
          <span
            @click.stop="pdfPreview(item, index)"
            :key="item.materialType"
            v-for="(item, index) in materialList"
            >《{{ item.materialTypeDesc || item.materialName }}》</span
          >
        </van-checkbox>
      </div>
      <div style="text-align: center; font-size: 12px; color: #bec1c4">
        建信人寿支持IPV6网络
      </div>
    </div>
    <!-- 投保激活 -->
    <div class="bottom-active">
      <a href="tel:95331"><img src=@/assets/image/phone_ccb.png alt></a>
      <div class="price">
        <div
          class="preferential"
          :data-title="(productData.discountName || '优惠价') + ' '"
          :data-unit="productData.discountUnit || '元'"
        >
          {{ productData.discountPrice || productData.initPyFAmt }}
        </div>
        <div
          class="original"
          :data-title="(productData.originalName || '原价') + ' '"
          :data-unit="productData.originalUnit || '元'"
          v-show="
            Number(productData.discountPrice || productData.initPyFAmt) <
            Number(productData.originalPrice || productData.initSumAmt)
          "
        >
          <del>{{ productData.originalPrice || productData.initSumAmt }}</del>
        </div>
      </div>
      <div class="active-button" @click="active(dataInfo)">
        {{ productData.btnDescribe || "立即投保" }}
      </div>
    </div>
    <!-- 滑块 -->
    <van-overlay
      :show="showOverlay"
      @click="showJigsaw && (showOverlay = false)"
    >
      <transition name="fade" mode="out-in">
        <jigsaw @success="jigsawSuccess" v-if="showJigsaw"></jigsaw>
      </transition>
      <transition name="fade" mode="out-in">
        <identify
          v-if="showIdentify"
          :phone="info.policyPhone"
          @checkCaptcha="checkCaptcha"
          @getCaptcha="getCaptcha"
          @click="showOverlay = false"
          @close="showOverlay = false"
        ></identify>
      </transition>
    </van-overlay>
    <van-overlay :show="serveInfo">
      <transition name="fade" mode="out-in">
        <div class="infoContent">
          <div class="bor">
            <div class="header">
              <b>服务人员信息</b>
              <div>
                <img
                  class="logos"
                  src="@/assets/image/logo.png"
                  alt=""
                  srcset=""
                />
              </div>
            </div>
            <div class="company">
              <div>
                <b class="font14">{{ referrerObj.referrerName }} </b>
                <span class="font12">建信人寿保险股份有限公司</span>
              </div>
              <div>
                <span class="font12">执业证号: </span>
                <span class="font12">{{ referrerObj.devno }}</span>
              </div>
            </div>
            <div class="footer">
              <div class="white">
                <img src="@/assets/image/phone.png" alt="" srcset="" />
              </div>
              <a
                v-bind:href="'tel:' + referrerObj.phone"
                class="textColor font12"
                >{{ referrerObj.phone }}</a
              >
              <i class="triangle"></i>
            </div>
          </div>
          <div class="cancle">
            <img
              @click="serveInfo = false"
              class="cancelImg"
              src="@/assets/image/cancel.png"
              alt=""
              srcset=""
            />
          </div>
        </div>
      </transition>
    </van-overlay>

    <van-popup
      v-model="showPdf"
      style="height: 100%; width: 100%"
      position="right"
    >
      <!-- 这是对多个产品条款的处理 -->
      <div v-if="proTerms" class="pdf">
        <div
          class="clause_item"
          v-for="(item, index) in productTerms"
          :key="index"
          v-show="readClause"
        >
          <div class="clause_name clause" @click="readTerm(index)">
                    {{ item.materialName.indexOf('《') == -1&&item.materialName.indexOf('》') ==-1?'《'+item.materialName+'》':item.materialName}}
          </div>
          <div class="clause">备案号：{{ item.description }}</div>
        </div>
        <div v-show="!readClause">
          <pdf
            :src="clausePdf"
            :page="currentPage"
            @num-pages="pageCount = $event"
            @loaded="currentPage = 1"
          ></pdf>
          <div class="pdfpage">
            <span @click="currentPage > 1 && currentPage--">上一页</span>
            <span
              >当前第{{ currentPage || 0 }}页/共{{ pageCount || "∞" }}页</span
            >
            <span @click="currentPage < pageCount && currentPage++"
              >下一页</span
            >
          </div>
          <div @click="agreeClause" class="agreeButton" style="bottom: 0px">
            我已阅读并同意
          </div>
        </div>
      </div>
      <!-- 这是对除了产品条款以外的所有pdf的处理 -->
      <div  v-if="!showWord&&!proTerms" class="pdf">
        <div
          v-if="pdfs[pdfIndex] && pdfs[pdfIndex].materialType && pdfs[pdfIndex].materialType=='M01'"
          class="description">
          产品条款:{{ pdfs[pdfIndex] && pdfs[pdfIndex].description }}
        </div>
        <pdf
          :src="pdfsrc"
          :page="currentPage"
          @num-pages="pageCount = $event"
          @loaded="currentPage = 1"   
        ></pdf>
        <div class="pdfpage">
          <span @click="currentPage > 1 && currentPage--">上一页</span>
          <span>当前第{{ currentPage || 0 }}页/共{{ pageCount || "∞" }}页</span>
          <span @click="currentPage < pageCount && currentPage++">下一页</span>
        </div>
        <div
          @click="previewPdf(++pdfIndex, hasAgree)"
          class="agreeButton"
        >
          我已阅读并同意
        </div>
      </div>
      <!--这是对所有json文件条款的处理  -->
      <div class="pdf" v-if="showWord&&!proTerms">
        <div class="word-content" style="font-weight: bold">{{ wordTip }}</div>
        <div
          class="word-content"
          v-for="(content, index) in words"
          v-html="content.content || content"
          :key="content + index"
        ></div>
        <div @click="previewPdf(++pdfIndex, hasAgree)" class="agreeButton">
          我已阅读并同意
        </div>
      </div>
    </van-popup>
    <van-popup v-model="showInfolay" position="bottom" safe-area-inset-bottom>
      <div class="elife-info">
        <div class="logo"><img src=@/assets/svg/logo.svg alt="">申请使用</div>
        <div class="info-content">
          {{ userInfo.policyName | filter("name") }}
        </div>
        <div class="info-content">
          {{ userInfo.policyIdCard | filter("id") }}
        </div>
        <div class="info-content">
          {{ userInfo.policyPhone | filter("phone") }}
        </div>
        <div class="info-bottom">
          <button class="no" @click="showInfolay = false">拒绝</button>
          <button class="yes" @click="useUserInfo(userInfo, true)">同意</button>
        </div>
      </div>
    </van-popup>
    <van-popup
      v-model="showNotice"
      style="height: 100%; width: 100%"
      position="bottom"
    >
      <div class="notice">
        <div class="notice_title"><span>健康告知</span>
          <span class="notice_return" @click="noticeReject">×</span>
        </div>
        
        <div
          v-for="item in productData.notifyList"
          :key="item.sortNum"
          class="notice-content"
        >
          {{ item.sortNum + "、" + item.notifyDesc }}
        </div>
        <div class="info-bottom">
          <button class="no" @click="noticeReject">部分为是</button>
          <button class="yes" @click="noticePass">全部为否</button>
        </div>
      </div>
    </van-popup>
    <!-- 特别提示 -->
    <van-overlay :show="showNoticeSwipe" @click="showNoticeSwipe = false">
      <div class="notice-swipe">
        <div
          class="notice-swipe-item"
          v-for="(item, index) in notice"
          :key="item.title + index"
          :style="{ '--x': (index - currentIndex) * 76 - 36 + 'vw' }"
          @click.stop
        >
          <div class="swipe-title">
            <img src=~@/assets/image/tip.png alt="">
            <br />
            {{ item.title }}
          </div>
          <div
            class="swipe-content"
            v-for="(v, i) in item.content"
            :key="v + i"
          >
            {{ v }}
          </div>
          <div class="swipe-confirm" @click="nextNotice">我已阅读并同意</div>
        </div>
      </div>
    </van-overlay>
    <div v-if="channelCode == '827'" class="serve" @click="serveInfo = true">
      <div>
        <img
          class="girlImg"
          src="@/assets/image/serveGirl.png"
          alt=""
          srcset=""
        />
      </div>
      <div class="serveText">服务人员</div>
    </div>
  </div>
</template>

<script>
import data from "./data.js";
import DatePicker from "@/components/date-picker";
import TimePicker from "@/components/time-picker";
import eInput from "@/components/input";
import jigsaw from "@/components/jigsaw";
import identify from "@/components/identify";
import { mapState, mapActions, mapGetters } from "vuex";
import pdf from "vue-pdf";
import dialog from "@/components/dialog/index.js";
import eArea from "@/components/area";
import md5 from "js-md5";
import util, {
  clearParams,
  getRelationShip,
  getlastDay,
  getDay,
  getcheckDay,
  reconfigArr,
} from "@/components/_utils";
import { TabbarItem } from "vant";

export default {
  components: {
    DatePicker,
    TimePicker,
    eInput,
    jigsaw,
    identify,
    pdf,
    eArea,
  },
  data() {
    return {
      ...data,
      // 只读属性
      readonly_iNa: false,
      readonly_iID: false,
      readonly_pNa: false,
      readonly_pID: false,
      readonly_pPh: false,
      // insureIdCard : '310115202005080010',
      dataInfo: {},
    };
  },
  computed: {
    ...mapState({
      userCode: (state) => state.app.userCode,
      phone: (state) => state.app.phone,
      userInfo: (state) => state.app.policyInfo,
      hasUserInfo: (state) => state.app.hasUserInfo,
      hasSomeUserInfo: (state) => state.app.hasSomeUserInfo,
      batch: (state) => state.app.batch,
      channelCode: (state) => state.app.channelCode,
      materialList: (state) => state.app.materialList,
      minDate: (state) => state.app.minDate,
      maxDate: (state) => state.app.maxDate,
      batchInfo: (state) => state.app.batchInfo,
      importActiveFlag: (state) => state.app.importActiveFlag,
      checkPhoneID: (state) => state.app.checkPhoneID,
      ENCR: (state) => state.app.exonerationNoticeCardRisk,
      hasCheckHold: (state) => state.app.hasCheckHold,
      batchNum: (state) => state.app.batchNo,
      routeQuery: (state) => state.app.routeQuery,
      imgCode: (state) => state.app.imgCode,
    }),
    ...mapGetters({
      productData: "product",
    }),
    src() {
      return this.$isPrd
        ? `${this.$baseUrl}statics/cardTemp/${this.productData.prd_id}.jpg`
        : this.productData.batchNo == "001"
        ? require("@/assets/image/qinwuyuan1.png")
        : require("@/assets/image/qinwuyuan2.png");
    },
    batchSrc() {
      return `${this.$baseUrl}statics/cardTemp/${
        this.batchInfo.batchNo || this.productData.batchNo
      }.jpg`;
    },

    insurePeriod() {
      const plan = this.productData.plans[0]; //至少一条保障
      let insurePeriod = "";
      switch (plan.insuYearFlag) {
        case "Y":
          insurePeriod = plan.insuYear + "年";
          break;
        case "M":
          insurePeriod = plan.insuYear + "个月";
          break;
        case "D":
          insurePeriod = plan.insuYear + "天";
          break;
        case "A":
          insurePeriod =
            "至被保险人年满" + plan.insuYear + "周岁后首个保单周年日";
          break;
        default:
          insurePeriod = "";
          break;
      }
      return insurePeriod;
    },
    agePeriod() {
      let min = this.productData.min_app_age
          ? this.productData.min_app_age
          : "出生满30天",
        max = this.productData.max_app_age
          ? this.productData.max_app_age + "周岁"
          : "出生满30天";
      return `${min}-${max}`;
    },
    showOriginal() {
      return this.productData.initPyFAmt > this.productData.initSumAmt;
    },
    occupationalGrade() {
      return this.productData.occupationalGrade;
    },
    rela() {
      if (
        this.productData.basicProfile &&
        this.productData.basicProfile.P025 === "Y"
      ) {
        return [
          // {
          //   code: "01",
          //   src: require("@/assets/svg/self.svg"),
          //   className: "me",
          //   name: "我自己",
          //   active: this.info.choosedRela === "01"
          // }
        ];
      }
      return [
        {
          code: "01",
          src: require("@/assets/svg/self.svg"),
          className: "me",
          name: "我自己",
          active: this.info.choosedRela === "01",
        },
        {
          code: "04",
          src: require("@/assets/svg/children.svg"),
          className: "children",
          name: "子女",
          active: this.info.choosedRela === "04",
        },
      ];
    },
    forWho() {
      const insure = this.rela.find((v) => v.code === this.info.choosedRela);
      return this.info.choosedRela === "01"
        ? "为本人投保"
        : "为" + insure.name + "投保";
    },
    check() {
      return {
        date: {
          required: true,
          title: "保单生效日期",
        },
        insureName: {
          required: this.info.choosedRela !== "01",
          title: "被保险人姓名",
          util: "checkName",
        },
        insureIdCard: {
