  filters: {
    filter(value, type) {
      switch (type) {
        case "id":
          return (
            value && value.replace(/(\d{4})\d{11}(\w{3})/, "$1***********$2")
          );
        case "name":
          return value && value.replace(/.(?=.)/g, "*");
        case "phone":
          return value && value.replace(/(\d{3})\d{4}(\d{4})/, "$1****$2");
        default:
          return value;
      }
    },
  },
