  methods: {
    ...mapActions([
      "checkHold",
      "cardCollectionImport",
      "mobileCheck",
      "orderInfoUpdate",
    ]),
    getScreenCap(url = "yeefxclick/yeefximg.js", type = "10000004") {
      this.TYPE = type
      this.$store.commit("GET_TYPE", this.TYPE);
      localStorage.setItem("TYPE",JSON.stringify(this.TYPE))
      localStorage.setItem("url",JSON.stringify(url))
      console.log("url",url)
      var param = {
        prodId: this.productData.prdId, //产品id
        time: +new Date(),
      };
      // console.log("param", param);
      this.$api.common.generateTraceId(param, true).then((res) => {
        if (res.status && res.status == 1 && res.data.traceFlag === "1") {
          // import("http://10.100.18.146/yeefxclick/count/js/yeefximg.js")
          if (res.data) {
            this.$store.commit("SET_IMG_CODE", res.data);
            localStorage.setItem("SET_IMG_CODE", JSON.stringify(res.data));
            console.log(res.data,"获取id")
            window._yfx_website = type || "10000004"; //可根据提供的文档替换
            //是否开启截图
            window._yfx_isopenimg = "auto"; //auto:dom元素记录和截图并存，dom:元素记录模式，img:截图模式，默认采用auto录屏模式
            //本次视频回溯的唯一标志会话id, 默认回溯系统会自动生成一个，也可以根据业务逻辑自行指定一个初始值；
            //如果要避免一直未提交投保单产生大量的数据，可以在每次进入投保首页的时候重新指定一个新的会话id（测试环境下推荐此方式）；
            //在指定的情况下，需要确保本值被优先初始化，然后调用后续的js代码，如果存在异步加载情况，优先使用方法_yfx_set_session('会话id');
            window._yfx_setimgsession = res.data.traceId;
            window._yfx_imgversion = res.data.verNo; //平台页面版本号
            window._yfx_imggoodsid = this.productData.prdId; //保险产品id
            window._yfx_imggoodsname = this.productData.prd_name; //保险产品名称
            window._yfx_imgpagenum = "1"; //页面节点编号，如第1个页面，可写1
            window._yfx_imgpagename = "信息录入页面"; //页面节点名称，如保险产品详情，可写保险介绍页面

            //开启调试，选填项，默认false关闭，true开启
            window._yfx_isdebug = false;
            let scriptDom = document.createElement("script");
            scriptDom.charset = "utf-8";
            scriptDom.src =
              location.origin +
              location.pathname.replace("/elife-card", "") +
              url;
            let scriptDoms = document.getElementsByTagName("script")[0];
            scriptDoms.parentNode.insertBefore(scriptDom, scriptDoms);
          }
        }else{
          localStorage.removeItem('SET_IMG_CODE')
        }
      });
    },
    getkeyLinkList(data){
      this.listKey = window._yfx_keyLinkConfigList(data)
      localStorage.setItem("listKeyArray",JSON.stringify(this.listKey.keyLinkConfigDataList))
      this.listKey.keyLinkConfigDataList.forEach( i => {
          this.codeList.push(i.keyLinkCode)
      })
      console.log(this.listKey,"listKeylistKeylistKey")
    },
    // 上传节点信息
    addLinkDetail(code){
      let ObjectList =  this.listKey.keyLinkConfigDataList.filter( i => i.keyLinkCode === code)
      let data = {
        imgSession :this.imgCode.traceId ?? '',  //会话id
        keyLinkName:ObjectList[0].keyLinkName, //基本信息
        keyLinkCode:ObjectList[0].keyLinkCode // 节点code
      }
      console.log(data,"data--1157")
      window._yfx_addKeyLinkDetail(data)
    },
    compare(prop) {
      return function (a, b) {
        var value1 = a[prop];
        var value2 = b[prop];
        return value1 - value2;
      };
    },
    chooseRela(item, disabled) {
