    foreverHandle(key) {
      this.$set(this.info, key + "Forever", !this.info[key + "Forever"]);
      if (this.info[key + "Forever"])
        return this.$set(this.info, key + "IdDate", "长期");
      this.$set(this.info, key + "IdDate", "");
