    checkCaptcha(smsCode) {
      let params = {
          prdId: this.productData.prd_id,
          mobileNo: this.info.policyPhone.replace(/\s/g, ""),
        },
        issmsCode = false;
      if (smsCode) params = { smsCode, ...params };
      if (!smsCode) params = { issmsCode, ...params };
      this.$api.common.checkCaptcha(params).then((res) => {
        console.log("验证码");
        this.$loading.hide();
        if (res.status === "1") {
          this.orderCode = res.data.orderCode;
          localStorage.setItem("ORDERCODE",JSON.stringify(res.data.orderCode))
          this.originPhone = this.info.policyPhone;
