    showData(dataInfo) {
      // console.log(this.info.choosedRela);

      let checkPhone = /^1[3-8][0-9]{9}/;
      let patt = new RegExp(checkPhone);

      console.log(dataInfo);

      if (dataInfo.cretType == "A" || dataInfo.cretType == "1010") {
        if (dataInfo.customerName && dataInfo.cretNo) {
          // 反算出生日期、性别
          this.getInfo(dataInfo.cretNo);
          // 返显信息
          // 姓名
          this.info.policyName = dataInfo.customerName;
          this.readonly_pNa = true;
          // this.readonly_pNa = false;

          // 证件号码
          this.info.policyIdCard = dataInfo.cretNo;
          this.readonly_pID = true;
          // this.readonly_pID = false;
        } else if (!dataInfo.customerName && dataInfo.cretNo) {
          // 反算出生日期、性别
          this.getInfo(dataInfo.cretNo);
          // 姓名可修改
          this.readonly_pNa = false;
          // 返显信息
          // 证件号码
          this.info.policyIdCard = dataInfo.cretNo;
          this.readonly_pID = true;
          // this.readonly_pID = false;
        } else if (dataInfo.customerName && !dataInfo.cretNo) {
          // 返显信息
          // 姓名
          this.info.policyName = dataInfo.customerName;
          this.readonly_pNa = true;
          // this.readonly_pNa = false;
        } else if (!dataInfo.customerName && !dataInfo.cretNo) {
          // 返显信息为空
          // 姓名可修改
          this.readonly_pNa = false;
          // 证件号码可修改
          this.readonly_pID = false;
        }

        // 手机号
        if (dataInfo.phoneNo && patt.test(dataInfo.phoneNo)) {
          this.info.policyPhone = dataInfo.phoneNo;
        }
      } else {
        if (dataInfo.customerName) {
          console.log(100);

          // 返显信息
          // 姓名
          this.info.policyName = dataInfo.customerName;
          this.readonly_pNa = true;
          // this.readonly_pNa = false;

          // 证件号可修改
          this.readonly_pID = false;
        } else if (!dataInfo.customerName) {
          // 姓名可修改
          this.readonly_pNa = false;
          // 证件号码可修改
          this.readonly_pID = false;
        }
        // 手机号
        if (dataInfo.phoneNo && patt.test(dataInfo.phoneNo)) {
          this.info.policyPhone = dataInfo.phoneNo;
        }
      }
    },

    phoneBlur(dataInfo) {
      // console.log(222);
      // console.log(this.info.policyPhone.replace(/ /g,""));
