    // 证件号码反算出生日期，性别
    getInfo(xinxi) {
      // 性别初始化
      let sex = null;
      // 出生日期初始化
      let birth = null;
      let myDate = new Date();
      let month = myDate.getMonth() + 1;
      let day = myDate.getDate();
      // 判断是否为居民身份证
      if (xinxi.length === 18) {
        // 性别判断值
        sex = xinxi.substring(16, 17);
        // 出生日期
        birth =
          xinxi.substring(6, 10) +
          "-" +
          xinxi.substring(10, 12) +
          "-" +
          xinxi.substring(12, 14);
      }
      // 性别
      if (sex % 2 === 0) {
        sex = 2; //'女'
      } else {
        sex = 1; //'男'
      }

      console.log(birth);
      console.log(sex);
    },

    // 验证数据
    checkData(checkdata1, checkdata2) {
      let checkID = /^[1-9]\d{5}[1-9]\d{3}((0[1-9])|(1[0-2]))(([0|1|2][1-9])|3[0-1])\d{4}$/;
      let checkPhone = /^1[3-8][0-9]{9}/;
      let patt1 = new RegExp(checkID);
      let patt2 = new RegExp(checkPhone);
      if (patt1.test(checkdata1)) {
        console.log("身份证验证通过");
      } else {
        console.log("身份证验证失败");
      }
      if (patt2.test(checkdata2)) {
        console.log("手机号验证通过");
      } else {
        console.log("手机号验证失败");
      }
    },

    active(dataInfo) {
      if (this.hasCheckHold) return;
      if (!this.hasAgree || !this.hasread) {
        return this.$dialog.alert({
          message: "请阅读后勾选相关条款须知",
          width: "60vw",
          confirmButtonText: "我知道了",
          className: "back-tip",
        });
      }
      if (!this.checkInfo()) {
        this.currentIndex = 0;
        let yntCodeStr = (
          clearParams(this.info.policyIdCard) +
          "YNT" +
          clearParams(this.info.policyPhone)
        )
          .toUpperCase()
          .replace(/\s+/g, "");
          console.log(this.routeQuery, "当type为3的时候不匹配");
        if (
          this.routeQuery.yntCode &&
          this.routeQuery.yntCode != md5(yntCodeStr)
        ) {
          console.log(this.routeQuery, "当type不为3的时候走这里");
          dialog.show({
            title: {
              text: "温馨提示：",
            },
            content: `非建行裕农通活动指定客户，无法免费领取。`,
            buttonText: "我知道了",
            callbak: function () {
              // _this.showNotice = false;
              return;
            },
          });
          return;
        }
        if (
          clearParams(this.info.policyPhone) === this.userInfo.policyPhone ||
          this.info.policyPhone === this.originPhone
        ) {
          // if(this.info.policyPhone.replace(/ /g,"") == dataInfo.phoneNo){
          //   this.showOverlay = false;
          //   }else{
          //     this.showOverlay = true;
          //   }
          this.checkCaptcha();
          console.log("bbb");
        } else {
          this.getGenerateImgDragVerify(dataInfo);
        }
      }
      this.$nextTick(() =>{
        if(this.codeList.includes('A04')){
          this.addLinkDetail('A04')
          console.log('A04--录入投保信息')
        }
      })
    },
    noticeReject() {
      const _this = this;
      dialog.show({
        title: {
          img: require("@/assets/image/tip.png"),
          text: "提示",
        },
        content: "抱歉，您未能通过投保审核，请选择其他保险卡",
        buttonText: "我知道了",
        callbak: function () {
          _this.showNotice = false;
        },
      });
    },
    noticePass() {
      this.showNotice = false;
      this.$nextTick().then(() => {
        if (this.notice.length > 0) {
          this.showNoticeSwipe = true;
        } else {
          this.cardActice();
        }
      });
    },
    cardActice() {
      this.showNoticeSwipe = false;
      // 阅读健康告知条款
      if(this.codeList.includes('A06')){
        this.addLinkDetail('A06')
        console.log('A06--阅读健康告知条款')
      }


      this.$nextTick().then(() => {
        console.log(this.checkPhoneID);
        if (this.checkPhoneID) {
          const option = {
            custName: this.params.plchdNm,
            cretNo: this.params.plchdCrdtNo,
            phoneNo: this.params.plchdMoveTelNo,
            cretType: "1010",
            prodId: this.params.prdId,
          };
          this.mobileCheck(option).then((res) => {
            console.log(res);
            if (res.status === "1") {
              this.beforeCheckHold();
            } else {
              console.log(res.error.message);
              return dialog.show({
                title: {
                  img: require("@/assets/image/tip.png"),
                  text: "提示",
                },
                content: res.error.message,
                buttonText: "我知道了",
              });
            }
          });
        } else {
          this.beforeCheckHold();
        }
      });
    },
    nextNotice() {
      this.currentIndex < this.notice.length - 1
        ? this.currentIndex++
        : this.cardActice();
    },
    getGenerateImgDragVerify(dataInfo) {
      if (this.info.policyPhone.replace(/ /g, "") == dataInfo.phoneNo) {
        this.showOverlay = false;
        console.log("验证1");

        this.checkCaptcha();
        // this.beforeCheckHold();
      } else {
        // this.showOverlay = true;
        this.$api.common
          .getGenerateImgDragVerify({
            randomId: this.getRandomId(),
          })
          .then((res) => {
            this.$loading.hide();
            if (res.status === "1") {
              this.imageCode = res.data.dragWidth / 3 - 10;
              this.showOverlay = true;
            } else if (res.status === "0") {
              this.$toast(res.error.message);
            }
            console.log("验证2");
            // this.checkCaptcha();
            // this.beforeCheckHold();
          });
      }
      console.log(this.showOverlay);
    },
    checkInfo() {
      const info = this.info,
        checkKeys = Object.keys(this.check);
      return checkKeys.find((v) => {
        const val = info[v],
          check = this.check[v];
        console.log(check);
        if (check.required) {
          if (!val) return this.$toast("请录入" + check.title + "！"), v;
        }
        if (val && check.util) {
          const res = util[check.util](val);
          return !res && (this.$toast(check.title + "录入有误！"), v);
        }
      });
    },
    getRandomId() {
      this.randomId = +new Date() + Math.random() * 100000 + 100000;
      return this.randomId;
    },
    setPrivates() {
      this.privates = {
        date: {
          disabled: false,
          filterType: "",
        },
        choosedRela: {
          disabled: false,
        },
        insureName: {
          disabled: false,
          filterType: "name",
        },
        insureIdCard: {
          disabled: false,
          filterType: "id",
        },
        policyName: {
          disabled: true,
          filterType: "name",
        },
        policyIdCard: {
          disabled: true,
          filterType: "id",
        },
        policyPhone: {
          disabled: false,
          filterType: "phone",
        },
      };
    },
    useUserInfo(info, privates) {
      if (privates) {
        this.setPrivates();
      } else {
        const keys = Object.keys(info); //返回可枚举的属性数组
        keys.forEach((v) => {
          if (v === "policyPhone") return;
          this.privates[v] && info[v] && (this.privates[v].disabled = true);
        });
        console.log(this.privates);
      }
      this.info = { ...this.info, ...info };
      Object.keys(this.info).forEach((v) => {
        !this.info[v] && (this.info[v] = "");
      });
      console.log(this.info, info);
      if (!this.info.choosedRela) this.info.choosedRela = "01";
      this.$nextTick().then(() => {
        this.showInfolay = false;
      });
    },
    readPdf() {
      if (this.materialList.length > 0) {
        this.pdfPreview(this.materialList[0], 0);
      } else {
        this.hasAgree = true;
      }
    },
    pdfPreview(item, index) {
      console.log("item,index", item, index);
      const productMaterialDetail = reconfigArr(this.materialList, index);
      Promise.all(
        productMaterialDetail.map((v) => {
          if (v.materialType) {
            return this.$api.common.getProductMaterialDetail({
              prdId: this.productData.prd_id,
              ...v,
            });
          } else {
            return v;
          }
        })
      ).then((materialList) => {
        // console.log(materialList,'这是材料');
        // for (let index = 0; index < materialList.length; index++) {
        //  let material=materialList[index].data
        // //  console.log(...material,'...material');
        //   if(materialList[index].status === "1"&&materialList[index].data[0].materialType=='M08'){
        //   materialList.splice(index,1)
        //   console.log(materialList,'11111222233334');}
          
        // }
        this.pdfs = materialList.reduce((init, res) => {
          console.log(res);
          let item = [];
          if (res.status === "1") {
            item = res.data.length ? res.data : [res.data];
          } else if (res.notPdf) {
            item = [res];
          } else {
            item = [];
          }
      //  对是否是产品条款的判断
          if (
            !item.hasOwnProperty("notPdf") &&
            item[0].materialType&&
            item[0].materialType == "M01"&&!this.hasAgree
          ) {
            this.productTerms=item.map((item)=>{
              this.hasread.push({ everyClause: false });
            return item
            })    
            
          }
          return init.concat(item);
        }, []);
        if (this.productTerms.length >= 2) { 
       this.pdfs=this.pdfs.filter((item)=>{
         return item.materialType != "M01"})
        } else {
          this.proTerms = false;
          this.hasread = true;
        }
        this.previewPdf(0);
        this.$loading.hide();
      });
    },

    readTerm(index) {
      this.hasread[index].everyClause = true;
      console.log(index);
      this.readClause = false;
      document.title = this.productTerms[index].materialName;
      let path = this.productTerms[index].materialPath;

      // materialPath:/upload/file/20210429/1619676826979008109.pdf
      //  let aa="http://localhost:8000/sign/images/upload/file/20210429/1619676826979008109.pdf"
      this.clausePdf = pdf.createLoadingTask(this.baseUrl + "images" + path);
      this.$loading.hide();
    },
    // 同意产品条款的处理
    agreeClause() {
      this.allRead = this.hasread.every(function (item) {
        return item.everyClause;
      });
      if (this.allRead) {
        this.proTerms = false;
        document.title = this.pdfs[0].materialName;
        this.currentPage=1
      } else {
        this.proTerms = true;
        this.readClause = true;
        document.title = "文件阅读";
      }
    },
    // 阅读文件的处理
    previewPdf(index, once) {
      console.log(once);
      if (once) {
        return (this.showPdf = false);
      }
      if (index >= this.pdfs.length) {
        return (this.showPdf = false), (this.hasAgree = true);
      }

      this.pdfIndex = index;
      this.showPdf = true;
      const material = this.pdfs[index];
      if (this.productTerms.length >= 2 && !this.allRead) {
        document.title = "文件阅读";
      } else {
        document.title = material.materialName;
      }

      this.showWord = false;
      if (this.pdfs[index].notPdf) {
        this.pdfsrc = "";
        this.words = material.content;
        return (this.showWord = true);
      }

      // const BASE_URL = process.env.VUE_APP_BASE_URL || "";
      // const baseUrl =
      //   location.origin +
      //   location.pathname.replace("/elife-card", "") +
      //   BASE_URL;
      // const material = this.pdfs[index];
      // res.data.find(v => v.materialName === item.materialTypeDesc) ||
      // res.data[index];
      const path = material.materialPath;
      // document.title = material.materialName;
      this.pdfsrc = pdf.createLoadingTask(this.baseUrl + "images" + path);
    },
    // 跳转到保障详情页
    todetail() {
      let param = {
        insurePeriod: this.insurePeriod,
        agePeriod: this.agePeriod,
        occupationalGrade: this.occupationalGrade,
        prdInsuranceDutyList: this.productData.prdInsuranceDutyList,
      };
      this.$router.push({
        name: "view-detail",
        params: param,
      });
    },
    // viewDetail() {
    //   //  "/card/file/M07/2022/08/30/220830160215000748.pdf"
    //   // "/card/file/2022/06/13/220613180146000804.pdf"
    //   // this.productData.occupationalGrade='1-2类'
    //   if (
    //     this.productData.materialPath &&
    //     this.productData.materialPath!= ""
