<template>
    <div>

    </div>
</template>
<script>
import skeleton from "@/components/skeleton";
import { mapState } from "vuex";
import md5 from "js-md5"
export default {
    data () {
        return{
            text:'',
            bankInfo:{}
        }
    },
    computed:{
        ...mapState({
            holdData:state => state.app.holdData,
            productData: state => state.app.productData,
            batchInfo: state => state.app.batchInfo,
            userInfo: state => state.app.policyInfo
        }),
    },
    methods:{
        callBack(){
            var requestObj  =  {
            action:'setCache',/*设置回调地址*/
            params:{
                key: this.bankInfo.ysId, // 回调的key，与下单参数的remark2保持一致
                value: location.origin + location.pathname.replace("/elife-card", '') + "elife-card/#/card/card-result/3/0/" + this.holdData.orderCode // 回调的url，支付成功后建行生活APP根据value跳转场景方url
                }
            }
            window.CCBBridge.requestNative(JSON.stringify(requestObj), 'callBacks'); 
        },
        payStatus(){
            console.log('支付回调')
        },
        MBC_PAYINFO(){
            var payInfo = {
                MERCHANTID:this.bankInfo.ccbMerchantId,	//商户代码	CHAR(15)	Y	T	由建行统一分配
                POSID:this.bankInfo.ccbPosId,	//柜台代码	CHAR(9)	Y	T	由建行统一分配
                BRANCHID:this.bankInfo.ccbBranchId,	//分行代码	CHAR(9)	Y	T	由建行统一分配
                POSID19:'',	//	商户19位终端号	CHAR(19)	N	F	由建行统一分配，仅作为参数传递，不参与MAC校验
                ORDERID:this.holdData.orderCode,	//	订单号	CHAR(30)	Y	T	由商户提供，最长30位
                PAYMENT:this.batchInfo.initPyFAmt,	//	付款金额	NUMBER(16,2)	Y	T	由商户提供，最长30位
                CURCODE:'01',	//	币种	CHAR(2)	Y	T	缺省为01－人民币（只支持人民币支付）
                TXCODE:'520100',	//	交易码	CHAR(6)	Y	T	由建行统一分配为520100
                REMARK1:'',	//	备注1	CHAR(30)	Y	T	网银不处理，直接传到城综网，该字段只支持送数字和英文
                REMARK2:this.bankInfo.ysId,	//	备注2	CHAR(30)	Y	T	上送YS开头的服务方编号
                TYPE:'1',	//	接口类型	CHAR(1)	Y	T	1- 防钓鱼接口
                GATEWAY:'0',	//	网关类型	CHAR(100)	Y	T	默认送0
                CLIENTIP:'',	//	客户端IP	CHAR(40)	N	T	客户在商户系统中的IP
                REGINFO:escape(this.holdData.plchdNm),	//	客户注册信息	CHAR(256)	N	T	客户在商户系统中注册的信息，中文需使用escape编码
                PROINFO:escape(this.productData.prd_name),	//	商品信息	CHAR(256)	N	T	客户购买的商品，中文需使用escape编码
                REFERER:'',	//	商户URL	CHAR(100)	N	T	商户送空值即可
                THIRDAPPINFO:'comccbpay1234567890cloudmerchant',	//	客户端标识	CHAR(40)	Y	T	通过建行生活APP下单场景，订单中客户端标识固定设为comccbpay1234567890cloudmerchant
                TIMEOUT:'',	//	订单超时时间	CHAR(14)	N	F	格式：YYYYMMDDHHMMSS（如：20120214143005）
                // 银行系统时间> TIMEOUT时拒绝交易，若送空值则不判断超时。
                // 当该字段有值时参与MAC校验，否则不参与MAC校验。
                PAYBITMAP:'',	//	支付位图	CHAR(10)	N	F	默认为空，只需要展示龙支付时请送0100000000
                // 当该字段有值时参与MAC校验，否则不参与MAC校验。
                // PLATFORMPUB:'MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCgAj+EgYdKHMHBWkr/XHjq5je7BrpG7vnjEnf8SqsZApmfjWDnXQu/TtYwFF9Z5ExoAPeLZK6J2/jBON8gjlppnOrEzIirUIt4x5yHEGq4io+lGtYsvShJmzR8It8tbKzVIb1laanJs9Vi4CtJZAVKn9axaTt9v8TuiZZ5oAy2WQIDAQAB',	//	服务方公钥	VARCHAR(256)	Y	F	仅作为源串参加MD5摘要，不作为参数传递
                PLATFORMPUB:this.bankInfo.serPublicKey,	//	服务方公钥	VARCHAR(256)	Y	F	仅作为源串参加MD5摘要，不作为参数传递
                MAC:'',	//	MD5加密串	CHAR(32)	Y	T	采用标准MD5算法，对以上字段进行MAC加密（32位小写），由商户实现。
                PLATFORMID:this.bankInfo.ysId,	//	服务方编号	CHAR(16)	Y	T	仅作为参数传递，不参与MAC校验
                ENCPUB:this.bankInfo.encryptPublicKey,	//	商户公钥密文	VARCHAR(512)	Y	F	使用服务方公钥对商户公钥后30位进行RSA加密并base64后的密文。
                // 若商户已经上架建行生活并同步公钥，可以不再上送商户公钥。仅作为参数传递，不参与MAC校
            }
            // 获取MAC验签
            // MERCHANTID=123456789&POSID=000000000&BRANCHID=110000000&ORDERID=19991101234
            // &PAYMENT=0.01&CURCODE=01&TXCODE=520100&REMARK1=&REMARK2=&TYPE=1&GATEWAY=&CLIENTIP=172.0.0.1
            // &REGINFO=%u5C0F%u98DE%u4FA0&PROINFO=%u5145%u503C%u5361&REFERER=&THIRDAPPINFO=comccbpay1234567890cloudmerchant
            // &TIMEOUT=20161028101226&PAYBITMAP=0100000000&MAC=4f9033be946e7dcd78886fdb4b2b0ec3
            // &PLATFORMID=YS0000000000000001&ENCPUB=ZGdXNUo3MXA5allmY0dUQm1mRW...
            // '+payInfo.REMARK2+'
            let str ='MERCHANTID='+payInfo.MERCHANTID+'&POSID='+payInfo.POSID+'&BRANCHID='+payInfo.BRANCHID+'&ORDERID='+
                      payInfo.ORDERID+'&PAYMENT='+payInfo.PAYMENT+'&CURCODE='+payInfo.CURCODE+'&TXCODE='+payInfo.TXCODE+
                     '&REMARK1=&REMARK2='+payInfo.REMARK2+'&TYPE='+payInfo.TYPE+'&GATEWAY='+payInfo.GATEWAY+'&CLIENTIP=172.0.0.1'+
                     '&REGINFO='+payInfo.REGINFO+'&PROINFO='+payInfo.PROINFO+'&REFERER=&THIRDAPPINFO='+payInfo.THIRDAPPINFO+
                     '&PLATFORMPUB='+payInfo.PLATFORMPUB
            let strs = 'MERCHANTID='+payInfo.MERCHANTID+'&POSID='+payInfo.POSID+'&BRANCHID='+payInfo.BRANCHID+'&ORDERID='+
                       payInfo.ORDERID+'&PAYMENT='+payInfo.PAYMENT+'&CURCODE='+payInfo.CURCODE+'&TXCODE='+payInfo.TXCODE+
                      '&REMARK1=&REMARK2='+payInfo.REMARK2+'&TYPE='+payInfo.TYPE+'&GATEWAY='+payInfo.GATEWAY+'&CLIENTIP=172.0.0.1'+
                      '&REGINFO='+payInfo.REGINFO+'&PROINFO='+payInfo.PROINFO+'&REFERER=&THIRDAPPINFO='+payInfo.THIRDAPPINFO
            payInfo.MAC = md5(str)
            let data = strs + '&MAC='+payInfo.MAC+'&PLATFORMID='+payInfo.PLATFORMID+'&ENCPUB='+ payInfo.ENCPUB
            return data;
        },
        MBS_DIRECT_PAY(payInfo){
            skeleton.hide()
            window.mbspay.directpay(payInfo); 
        },
        MBS_DIRECT_PAYS(){
            skeleton.hide()
            window.location="mbspay://direct?"+ this.MBC_PAYINFO();
        },
        payCheck(payInfo){
            // 根据手机系统，唤起建行生活支付收银台方法不同
            const ua = navigator.userAgent.toLowerCase();
            if (ua.indexOf("android") != -1) {
                /**
                 * 调起收银台
                 * @param {String} payInfo payInfo为支付参数（支付参数详见支付SDK《商户下单支付参数文档》）
                 */
                // 苹果支付
                this.MBS_DIRECT_PAY(payInfo)
            } else if (ua.indexOf("iphone") != -1) {
                this.MBS_DIRECT_PAYS(payInfo)
            }
        }
    },
    mounted() {
        window.callBacks =  this.callBack()
        
    },
    created(){
        // eruda.init()
        this.bankInfo = this.$route.params.payData
        if (this.bankInfo) {
            sessionStorage.setItem('ccbPosid',this.bankInfo.ccbPosId)
            this.callBack()
            skeleton.show()
            this.payCheck(this.MBC_PAYINFO())
        }else{
            this.$router.go(-1)
        }
    }
}
</script>