<template>
  <div class="box">
    <van-nav-bar
      title="订单详情"
      right-text=""
    />
    <!-- 龙耀一世B款终身寿险 -->
    <!-- detail.data.prdName -->
    <div class="header">{{ prdName }}</div>
    <div class="line"></div>
    <div class="order-detail">
      <div
        v-for="(item, index) in orderDetail"
        :key="item.orderDes"
        class="detail_insurance"
      >
        <span class="left">{{ item.orderDes }}</span>
        <span :class="['right', { money: index == 2 ? true : false }]">{{
          item.detailDes
        }}</span>
      </div>
    </div>
    <div class="insurance-detail">
      <div
        v-for="item in insuranceDetail"
        :key="item.insuranceDes"
        class="detail_insurance"
      >
        <span class="left">{{ item.insuranceDes }}</span>
        <span class="right">{{ item.detailDes }}</span>
      </div>
    </div>
    <van-button
      type="primary"
      color="#4ca2fe"
      block
      class="payButton"
      @click="topay"
      v-if="paymentWait"
      >立即支付</van-button
    >
  </div>
</template>

<script>
import { Divider, Toast } from "vant";
import Vue from "vue";
import { NavBar } from "vant";
import loading from "@/components/loading/index.js";
import skeleton from "@/components/skeleton";
Vue.use(NavBar);
import { mapState, mapActions, mapGetters } from "vuex";
// import { Toast } from "vant";
export default {
  name: "view-detail",
  data() {
    return {
      selectOrderListRes:'',
      beOverDue: false,
      params: "", //路由传递过来的参数
      prdName: "龙耀一世B款终身寿险",
      payOrder: "", //订单列表中要支付的哪一个
      detail: "", //订单详情
      loginDetail: "", //登陆信息
      insurerCerId: "", //证件类型
      paymentWait: true, //是否展示立即支付按钮，.payStatus	支付状态	数值类		Short	[0-待支付；1-支付中;2-支付成功;3-支付失败;4-微信授权转账中]
      decrypt: "", //解密的信息
      orderDetail: [
        {
          orderDes: "订单日期:",
          detailDes: "2022-01-18 16:01:22",
        },
        {
          orderDes: "订单号码:",
          detailDes: "12345234567892345678",
        },
        {
          orderDes: "订单金额:",
          detailDes: "1234元",
        },
      ],
      insuranceDetail: [
        {
          insuranceDes: "投保人:",
          detailDes: "李四",
        },
        {
          insuranceDes: "性别:",
          detailDes: "男",
        },
        {
          insuranceDes: "证件号码:",
          detailDes: "411526200003196729",
        },
        {
          insuranceDes: "出生日期:",
          detailDes: "1980-01-01",
        },
      ],
    };
  },
  computed: {},
  methods: {
    topay() {
         const ua = navigator.userAgent.toLowerCase();
         let platForm;
      if (ua.indexOf("android") != -1) {
        platForm = "android";
      } else if (ua.indexOf("iphone") != -1) {
        platForm = "iPhone";
      } else {
        platForm = "pc";
      }
      let payData = {
        backUrlType:
          "elife-card/#/card/card-result/3/0/" + this.detail.data.orderCode, // 回退的url
        branchCode: this.detail.data.recentRecord.branchCode, //组织机构代码
        payTypeCode: this.selectOrderListRes.data.payTypeCode, //支付类型code 《00：默认的卡单龙支付惠购  01: h5新龙支付》
        cardSaleType: this.selectOrderListRes.data.cardSaleType, // 卡单销售类型
        ccbMerchantId: this.selectOrderListRes.data.merchantId, //  龙支付惠购商户号
        ccbPosId: this.selectOrderListRes.data.posId, // 龙支付惠购柜台号
        opendId: localStorage.getItem("elife-openid") || "blank",
        wxCode: sessionStorage.getItem("elife-wxCode") || "",
        customerId: this.loginDetail.data.cusId || "",
        orderCode: this.detail.data.orderCode, //保单编号
        payAmount: this.detail.data.recentRecord.payAmount, // 保单金额
        platForm, //安卓还是苹果
        browserType:
          this.detail.data.policyList[0].channelCode === "0011" ||
          this.detail.data.policyList[0].channelCode === "9999"
            ? "01"
            : "02", //holdData核保的params
        channelCode: this.detail.data.policyList[0].channelCode, //销售渠道编码
        timestamp: new Date().getTime() + Math.random(),
        paySign: "", //验签，
        userId: this.decrypt.data.USERID,
        initSumAmt: this.selectOrderListRes.data.initSumAmt, //订单原金额
        initPyFAmt: this.selectOrderListRes.data.initPyFAmt, //订单实际支付金额
        coPyFAmt: this.selectOrderListRes.data.coPyFAmt, //第三方平台优惠金额
        discountAmtDesc: this.selectOrderListRes.data.discountAmtDesc || "", //第三方平台优惠定义
      };
      // 验签参数
      var payReqData = {
        branchCode: this.detail.data.recentRecord.branchCode, //组织机构代码
        payAmount: this.detail.data.recentRecord.payAmount, // 保单金额
        orderCode: this.detail.data.orderCode,
        // orderCode:'20221018000005247618'
      };
      // 获取验签
      this.api.card.checkNewPay(payReqData).then((paywayres) => {
        payData.paySign = paywayres.data.paySign;
        payData.trackvideo = paywayres.data.payNewSign || "";
        if (paywayres.status == "1") {
          // 订单推送接口
           this.api.card.mebOrderPush(payData, true).then((res) => {
          if (res.status == "1") {
          router.replace({
            name: "blank-page",
            params: {
              payData:res.data,
            },
          });
        } else {
          router.replace({
            name: "card-result",
            params: {
              type: "0",
              message:res.error.message,
            },
          });
        }
      });
        }else if(paywayres.status == "0"){
            skeleton.hide();
           loading.hide();
           this.$router.replace({
          name: "card-result",
          params: {
          type: "0",
          // message: this.selectOrderListRes.error.message
          message: paywayres.error.message
        }
      });

        }
      });
      console.log(payData, "payData");
    }
  },
  async created() {
    skeleton.show();
    let orderCode = this.$route.query.orderNo;
    let ccbParamSJ = this.$route.query.ccbParamSJ;
    // 调新增详情接口
       // 获取详情
   this.selectOrderListRes=await this.api.card.selectOrderList({
          //  orderCode:'20221018000005247618'
           orderCode:orderCode
           }, true)
    if(this.selectOrderListRes.status=='1'){
      this.$store.commit("CONTINUE_PAY_MES", this.selectOrderListRes.data);
      // 调解密串
      this.decrypt= await this.api.card.encrypt({ cipherText: ccbParamSJ,ccbPosId:this.selectOrderListRes.data.posId });
       if(this.decrypt.status=='1'){
      // 登陆
     this.loginDetail = await this.api.card.ccbLifeLogin({
         phoneNo:this.decrypt.data.mobile
        //  phoneNo:'15353535353'
         });
         if(this.loginDetail.status=='1'){
            sessionStorage.setItem(
          "elife-userData",
          JSON.stringify(this.loginDetail.data) 
        );
      const channelCode = sessionStorage.getItem("channelCodeOld");
     let payOnOrderList  = await this.api.card.payOnOrderList ({
      orderCode:orderCode,
      queryTimeArea: "0",
      channelCode: channelCode,
    });
    if(payOnOrderList.status=='1'){
 this.detail = await this.api.card.orderDetail({
      orderCode: orderCode,
      channelCode: channelCode,
    });
       if (this.detail.status == "1") {
      switch (this.detail.data.orderCretType) {
        case "1010":
          this.insurerCerId = "居民身份证";
          break;
        case "1011":
          this.insurerCerId = "临时居民身份证";
          break;
        case "1021":
          this.insurerCerId = "军人身份证件";
          break;
        case "1031":
          this.insurerCerId = "武警身份证件";
          break;
        case "1040":
          this.insurerCerId = "户口薄";
          break;
        case "1050":
          this.insurerCerId = "护照";
          break;
        case "1051":
          this.insurerCerId = "中国护照";
          break;
        case "1060":
          this.insurerCerId = "学生证";
          break;
        case "1140":
          this.insurerCerId = "香港居民身份证";
          break;
        case "1150":
          this.insurerCerId = "澳门居民身份证";
          break;
        case "1160":
          this.insurerCerId = "台湾居民身份证";
          break;
        case "1730":
          this.insurerCerId = "出生证明";
          break;
        case "1700":
          this.insurerCerId = "外国身份证";
          break;
        case "1740":
          this.insurerCerId = "公民身份证明";
          break;
        default:
          this.insurerCerId = "其他证件";
          break;
      }
      this.prdName = this.detail.data.prdName;
      this.orderDetail[0].detailDes = this.detail.data.orderSubmitTime;
      this.orderDetail[1].detailDes = this.detail.data.orderCode;
      this.orderDetail[2].detailDes =
      this.selectOrderListRes.data.initSumAmt + "元";
      this.insuranceDetail[0].detailDes = this.detail.data.insurerName;
      this.insuranceDetail[1].detailDes =
      this.detail.data.insurerSex == 1 ? "男" : "女";
      this.insuranceDetail[2].detailDes =
      this.insurerCerId + "\n" + this.detail.data.insurerCerId;
      this.insuranceDetail[3].detailDes = this.detail.data.birthday;
      let patState = this.detail.data.recentRecord.payStatus;
      skeleton.hide();
      loading.hide();
      // 24小时内订单支付的处理
      let datatime = this.detail.data.orderSubmitTime;
      let timeHandle = datatime.split("-").join("").split(":").join("");
      let ordertime = parseInt(timeHandle.replace(/\s+/g, ""));
      console.log(ordertime, "newtime2");
      let nowTime = new Date();
      let y = nowTime.getFullYear();
      let m = nowTime.getMonth() + 1;
      let d = nowTime.getDate();
      let h = nowTime.getHours();
      let f = nowTime.getMinutes();
      let ss = nowTime.getSeconds();
      let s = 0;
      ss < 10 ? (s = "0" + nowTime.getSeconds()) : (s = nowTime.getSeconds());
      let nowTimers = parseInt(y + "" + m + d + h + f + s);
      console.log(nowTimers, ordertime);
      console.log(nowTimers - ordertime, "差");
      if (nowTimers - ordertime > 1000000) {
        this.beOverDue = true;
        console.log("大于24小时");
      }
      // 到这里
      patState == 0 || patState == 1 || patState ===3
        ? (this.paymentWait = true)
        : (this.paymentWait = false);
      this.beOverDue ?this.paymentWait = false:this.paymentWait = true
        
    }else{
      skeleton.hide();
      loading.hide();
       this.$router.replace({
        name: "card-result",
        params: {
          type: "0",
          // message: this.selectOrderListRes.error.message
          message: this.detail.error.message
        }
      });
    }
    }
    }else{
         skeleton.hide();
      loading.hide();
       this.$router.replace({
        name: "card-result",
        params: {
          type: "0",
          // message: this.selectOrderListRes.error.message
          message: this.loginDetail.message
        }
      });
    }
         
        }
    }else if(this.selectOrderListRes.status=='0'){
      skeleton.hide();
      loading.hide();
       this.$router.replace({
        name: "card-result",
        params: {
          type: "0",
          message: this.selectOrderListRes.error.message
          // message: '您的订单查询结果为空'
        }
      });
    }
   
  },
};
</script>

<style lang="less" scoped>
@marginOfForm: 35.5vw;
@cardHeight: 12.5vw;
@gray: #efefef;
@font10: 2.7vw;
@font12: 3.2vw;
@font14: 3.7vw;
@font16: 4.2vw;
@font18: 4.8vw;
.box {
  width: 100%;
  height: 100vh;
  background-color: #e7e3e7;
  position: relative;
  .van-nav-bar {
    height: 7vh;
    line-height: 7vh;
    /deep/ .van-nav-bar__title {
      font-size: @font18;
    }
    /deep/ .van-nav-bar__text {
      font-size: @font18;
      color: #303133;
    }
  }
  .header {
    font-size: @font18;
    height: 6vh;
    line-height: 7vh;
    text-align: left;
    font-weight: 550;
    padding-left: 20px;
    background-color: #fffeff;
    margin-top: 10px;
  }
  .line {
    height: 1px;
    background-color: #e7e3e7;
    width: 92vw;
    margin: 0px 20px;
  }
  .order-detail {
    width: 100%;
    background-color: #fffeff !important;
  }
  .detail_insurance {
    width: 100%;
    font-size: @font16;
    line-height: 2.7em;
    color: #303133;
    height: 2.7em;
    .left {
      content: attr(data-des);
      float: left;
      margin-left: 20px;
      width: 23vw;
      text-align: left;
    }
    .right {
      content: attr(data-detail);
      float: left;
    }
    .money {
      color: #ebae63;
    }
  }
  .order-detail {
    width: 100%;
    background-color: rgb(187, 173, 176);
  }

  .insurance-detail {
    width: 100%;
    background-color: #fffeff;
    margin-top: 14px;
  }
  .payButton {
    position: absolute;
    bottom: 30px;
    width: 94%;
    height: 40px;
    margin-left: 3%;
    border-radius: 20px;
  }
}
</style>

